body {
  margin: 0px;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Alphakind';
}

.App {
  width: 100%;
  height: 100%;
}

#game-canvas {
  width: 100%;
  height: 100%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number]{
  -moz-appearance: textfield;
}